body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.gallery-container {
  display: grid;
  flex-wrap: wrap;
  grid-template-columns: repeat( auto-fit, minmax(200px, 1fr));
  padding-top: .9375rem;
}

.gallery-content-loaded {
  opacity: 1;
  -webkit-transition: 3s;
  transition: 3s;
  max-width: 200px;
 height: auto;
 border-radius: 6px;
}

.gallery-content-loading {
  opacity: 0;
  -webkit-transition: 3s;
  transition: 3s;
  max-width: 200px;
 height: auto;
 border-radius: 6px;
}

img#thumbnail:hover {
  cursor: pointer;
  opacity: 0.4;
  -webkit-transition: 0.1s;
  transition: 0.1s;
}



.page-overlay {
    position:fixed;
    background-color: black;
    width:100%;
    height:100%;
    top:0px;
    left:0px;
    z-index:1000;
}

.modal-overlay {
 position: absolute;
 top: 0;
 left: 0;
 right: 0;
 bottom: 0;
 z-index: 10;
 width: 100%;
 height: 100%;
 background: rgba(21,21,21,.75);
 position: fixed;
}


.modal-body {
 opacity: 1;
 position: fixed;
 top: 50%;
 left: 50%;
 z-index: 11;
 padding: 0;
 overflow: auto;
 border-radius: 6px;
 -webkit-transform: translate(-50%, -50%);
         transform: translate(-50%, -50%);
 -webkit-transition: 2s all;
 transition: 2s all;
}

.img-modal {
	max-width: 800px;
	max-height: 500px;
}

#close {
	color: #000000;
	opacity: 0.5;
	position: absolute;
	top: 0;
	right: 30%;
	
}

#close:hover {
	opacity: 0.8;
}

#prev {
    position: absolute;
    bottom: 50%;
    left: 15%;
}

#next {
	position: absolute;
	bottom: 50%;
	right: 15%;
}

#prev, #next {
    text-decoration: none;
    display: inline-block;
    padding: 15px;
    color: black;
    background-color: white;
    opacity: 0.2;
}

#prev:hover, #next:hover {
	color: brown;
	opacity: 0.4;
}



.round {
    border-radius: 60%;
}
.App {
  text-align: center;
  top: 0;
  padding: 0;
  border: 0;
}

.photography-body {
  padding-left: 10%;
  padding-right: 10%;
}

h1 {
  color: #FFDFDE;
}

@media only screen and (max-width: 700px) {
  h1 {
  font-size: 5vw;
  }
}


